var Main_index;
var Main_Title=[];//标题数组
var Main_Content=[];//正文数组

export default {
	data() {
		return {
			
		}
	},
	methods: {
		det(){
			uni.navigateTo({
				url:'../det/det'
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor.1"
			})
		}
	},
	onLoad() {
		Main_Title=[];//标题数组
		Main_Content=[];//正文数组
		uni.getStorage({
			key:"PM_ID",
			success: (res) => {
				Main_index = res.data;
				console.log(Main_index);
				for (var i = 1;i <= Main_index ;i++) {
					uni.getStorage({
						key:i + "PM_Title",
						success: (res) => {
							Main_Title.push(res.data);
							console.log(Main_Title);
							}
						})
					uni.getStorage({
						key:i + "PM_Content",
						success: (res1) => {
							Main_Content.push(res1.data);
							console.log(Main_Content)
						}
					})
				}
			}
		})
	}
}
